<template>
  <b-container>
    <!--<b-jumbotron header="爱问悬赏问答" lead="基于星云链的悬赏问答网站" >-->
    <!--</b-jumbotron>-->
    <transition name="fade">
      <b-row>
        <b-col cols="12">
          <h3>爱情宣言</h3>
        </b-col>
        <b-col cols="12" v-for="love in loveList" :key="love.title">
          <b-card :title="love.title"
                  :img-src="love.image"
                  tag="article">
            <p class="card-text">
              {{sub(love.message)}}
            </p>
          </b-card>
        </b-col>
        <div class="container" @click="more">
          <p class="muted credit text-center">点击加载更多</p>
        </div>
      </b-row>
    </transition>
  </b-container>
</template>

<script>
import api from '../libs/api'

export default {
  name: 'Home',
  data () {
    return {
      loveList: [],
      offset: 0,
      pageNum: 20
    }
  },
  created () {
    api.getByDesc(this.offset, this.pageNum, resp => {
      this.loveList = this.loveList.concat(JSON.parse(resp.result))
      this.offset = this.loveList.length
    })
  },
  methods: {
    more (evt) {
      api.getByDesc(this.offset, this.pageNum, resp => {
        this.loveList = this.loveList.concat(JSON.parse(resp.result))
        this.offset = this.loveList.length
      })
    },
    sub (message) {
      if (message.length > 100) {
        return message.substring(0, 100) + '...'
      } else {
        return message
      }
    }
  }
}
</script>

<style scoped>
  #container {
    box-sizing: border-box;
    margin-top: 3rem;
    width: 100vw;
    margin-bottom: 54px;
  }
</style>



