<template>
  <b-container>
    <!--<b-jumbotron header="爱问悬赏问答" lead="基于星云链的悬赏问答网站" >-->
    <!--</b-jumbotron>-->
    <b-row>
      <b-col cols="12">
        <h3>我的爱情宣言</h3>
      </b-col>
      <b-col cols="12" v-for="love in loveList" :key="love.title">
        <b-card :title="love.title"
                :img-src="love.image"
                tag="article">
          <p class="card-text">
            {{love.message}}
          </p>
        </b-card>
      </b-col>
    </b-row>
  </b-container>
</template>

<script>
import api from '../libs/api'

export default {
  name: 'MyLove',
  data () {
    return {
      loveList: []
    }
  },
  created () {
    api.getSelf(resp => {
      this.loveList = this.loveList.concat(JSON.parse(resp.result))
      this.offset = this.loveList.length
    })
  }
}
</script>

<style scoped>
  #container {
    box-sizing: border-box;
    margin-top: 3rem;
    width: 100vw;
    margin-bottom: 54px;
  }
</style>



