<template>
  <b-container>
    <b-row>
      <b-form @submit="onSubmit" @reset="onReset" class="col-md-12">
        <b-form-group id="messageG"
                      label="爱情宣言主题:"
                      label-for="exampleInput1">
          <b-form-input id="message"
                           type="text"
                           v-model="form.title"
                           required
                           placeholder="输入爱情宣言主题">
          </b-form-input>
        </b-form-group>
        <b-form-group id="messageG"
                      label="爱情宣言甜言蜜语:"
                      label-for="exampleInput1">
          <b-form-textarea id="message"
                        type="text"
                        v-model="form.message"
                        :rows="3"
                        :max-rows="6"
                        required
                        placeholder="输入爱情宣言甜言蜜语">
          </b-form-textarea >
        </b-form-group>
        <b-form-group id="imageG"
                      label="爱情照片:"
                      label-for="exampleInput1">
          <b-form-file v-model="form.file" :state="Boolean(form.file)" placeholder="选择爱情照片..."></b-form-file>
        </b-form-group>
        <b-button type="submit" variant="primary">提交</b-button>
        <b-button type="reset" variant="danger">清空</b-button>
      </b-form>
    </b-row>
  </b-container>
</template>
<script >
import api from '../libs/api'
import image from '../libs/image'

export default {
  name: 'CreateLove',
  data () {
    return {
      form: {
        title: '',
        message: '',
        file: ''
      }
    }
  },
  methods: {
    onSubmit (evt) {
      evt.preventDefault()
      image.PhotoCompress(this.form.file, {
        quality: 0.8,
        width: 640
      }, re => {
        api.publishLove(this.form.title, this.form.message, re, resp => {
        })
      })
    },
    onReset (evt) {
      evt.preventDefault()
      this.form.title = ''
      this.form.message = ''
      this.form.file = ''
      this.$nextTick(() => { this.show = true })
    }
  }
}

</script>



