<template>
  <b-container :key="new Date().getMilliseconds()">
    <b-row>
      <b-col cols="12">
        <Head></Head>
        <transition name="fade">
          <keep-alive>
            <router-view id="container"></router-view>
          </keep-alive>
        </transition>
        <Footer></Footer>
      </b-col>
    </b-row>
  </b-container>
</template>

<script>
import Head from '@/components/Head'
import Footer from '@/components/Footer'

export default {
  name: 'overview',
  components: {
    Head,
    Footer
  }
}
</script>

<style scoped>
  #container {
    box-sizing: border-box;
    margin-top: 3rem;
    width: 100vw;
    margin-bottom: 54px;
  }
</style>



